  display: flex;
  align-items: center;
  gap: 10px;
}

.form__label {
  display: inline-block;
  margin-bottom: 5px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--label);
}

.form__input {
  position: relative;
  border: 1px solid var(--border);
  border-radius: 5px;
  padding: 10px;
  width: 100%;
}

.form__input:focus {
  border: 1px solid var(--focus);
}

.form__input::input-placeholder {
  font-size: 14px;
  color: var(--border);
}

.form__input::placeholder {
  font-size: 14px;
  color: var(--border);
}

.form__input:input-placeholder {
  font-size: 14px;
  color: var(--border);
}

.form__input::input-placeholder {
  font-size: 14px;
  color: var(--border);
}

.form__input::placeholder {
  font-size: 14px;
  color: var(--border);
}

.phone-input {
  padding-left: 40px;
}

.wrapper-input {
  position: relative;
}

.country {
  position: absolute;
  left: 1px;
  top: 1px;
  bottom: 1px;
  display: flex;
  align-items: center;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding: 0 9px;
  background-color: var(--bg);
  cursor: pointer;
  gap: 10px;
}

@media (max-width: 400px) {
  .form__wrapper {
    flex-direction: column;
  }
  .form__btn {
    max-width: 100%;
  }
}